@charset "UTF-8";
shiny-tool-result {
  display: block;
  margin: 1em 0;
  border-radius: var(--bs-border-radius, 4px);
  overflow: visible;
  padding: 0;
  font-size: 0.8em;
}
shiny-tool-result .request-intent {
  margin-inline-start: auto;
  opacity: 0.66;
  font-style: italic;
  font-weight: normal;
}
shiny-tool-result .tool-icon {
  min-width: 16px;
  display: flex;
  align-items: center;
}
shiny-tool-result pre {
  margin: 0;
  white-space: pre-wrap;
  padding: 1em;
  border-radius: var(--bs-border-radius, 4px);
}
shiny-tool-result .shiny-tool-result-card {
  max-height: var(--shiny-tool-result__max-height, 500px);
}
shiny-tool-result .shiny-tool-result-card .card-header,
shiny-tool-result .shiny-tool-result-card .card-footer {
  font-size: inherit;
  font-weight: normal;
  word-break: break-word;
}
shiny-tool-result .shiny-tool-result-card .card-header {
  width: 100%;
  text-align: left;
  border: none;
  background: none;
  cursor: pointer;
}
shiny-tool-result .shiny-tool-result-card .card-header:hover, shiny-tool-result .shiny-tool-result-card .card-header:focus-visible {
  background-color: rgba(var(--bs-emphasis-color-rgb), 0.05);
  outline: none;
}
shiny-tool-result .shiny-tool-result-card .card-header .function-name {
  font-weight: bold;
}
shiny-tool-result .shiny-tool-result-card .card-header .collapse-arrow {
  transition: transform 0.2s ease-in-out;
}
shiny-tool-result .shiny-tool-result-card .card-header[aria-expanded=true] .collapse-arrow {
  transform: rotate(-90deg);
}
shiny-tool-result .shiny-tool-result-card .card-body {
  transition: max-height 0.3s ease-out, opacity 0.2s ease-out;
  overflow: hidden;
  opacity: 1;
}
shiny-tool-result .shiny-tool-result-card .card-body.collapsed {
  max-height: 0;
  opacity: 0;
  padding: 0;
}

shiny-tool-request {
  margin: 1em 0;
  border-radius: var(--bs-border-radius, 4px);
  overflow: hidden;
  padding: 0.5em;
  font-size: 0.8em;
  color: var(--bs-secondary, #6c757d);
  font-weight: 400;
  display: flex;
  align-items: center;
  user-select: none;
}
shiny-tool-request[hidden] {
  display: none;
}
shiny-tool-request::before {
  content: "⬅";
  /* UTF-8: \u2B05 */
  color: var(--bs-primary, #0d6efd);
  display: inline-block;
  margin-right: 0.5em;
  font-size: 1em;
  transform-origin: center;
  transform: rotateY(180deg);
}
shiny-tool-request .function-name {
  font-weight: bold;
}
shiny-tool-request .request-intent {
  color: var(--bs-secondary, #6c757d);
  font-style: italic;
  margin-left: 0.5em;
}

shiny-chat-container {
  --shiny-chat-border: var(--bs-border-width, 1px) solid var(--bs-border-color, #e9ecef);
  --shiny-chat-user-message-bg: RGBA(var(--bs-primary-rgb, 0, 123, 194), 0.06);
  --_chat-container-padding: 0.25rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  margin: 0 auto;
  gap: 0;
  padding: var(--_chat-container-padding);
  padding-bottom: 0;
}
shiny-chat-container p:last-child {
  margin-bottom: 0;
}
shiny-chat-container .suggestion,
shiny-chat-container [data-suggestion] {
  cursor: pointer;
}
shiny-chat-container .suggestion {
  color: var(--bs-link-color, #007bc2);
  text-decoration-color: var(--bs-link-color, #007bc2);
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
  text-underline-offset: 4px;
  text-decoration-thickness: 2px;
  padding-inline: 2px;
}
shiny-chat-container .suggestion:hover {
  text-decoration-style: solid;
}
shiny-chat-container .suggestion::after {
  content: "✦";
  display: inline-block;
  margin-inline-start: 0.15em;
}
shiny-chat-container .suggestion.submit::after, shiny-chat-container .suggestion[data-suggestion-submit=""]::after, shiny-chat-container .suggestion[data-suggestion-submit=true]::after {
  content: "↵";
}
shiny-chat-container .card[data-suggestion]:hover {
  color: var(--bs-link-color, #007bc2);
  border-color: rgba(var(--bs-link-color-rgb), 0.5);
}

shiny-chat-messages {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  overflow: auto;
  margin-bottom: 1rem;
  --_scroll-margin: 1rem;
  padding-right: var(--_scroll-margin);
  margin-right: calc(-1 * var(--_scroll-margin));
}

shiny-chat-message {
  display: grid;
  grid-template-columns: auto minmax(0, 1fr);
  gap: 1rem;
  /* Vertically center the 2nd column (message content) */
}
shiny-chat-message > * {
  height: fit-content;
}
shiny-chat-message .message-icon {
  border-radius: 50%;
  border: var(--shiny-chat-border);
  height: 2rem;
  width: 2rem;
  display: grid;
  place-items: center;
  overflow: clip;
}
shiny-chat-message .message-icon > * {
  height: 100%;
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  margin: 0 !important;
  object-fit: contain;
}
shiny-chat-message .message-icon > svg,
shiny-chat-message .message-icon > .icon,
shiny-chat-message .message-icon > .fa,
shiny-chat-message .message-icon > .bi {
  max-height: 66%;
  max-width: 66%;
}
shiny-chat-message .message-icon:has(> .border-0) {
  border: none;
  border-radius: unset;
  overflow: unset;
}
shiny-chat-message shiny-markdown-stream {
  align-self: center;
}

/* Align the user message to the right */
shiny-user-message {
  align-self: flex-end;
  padding: 0.75rem 1rem;
  border-radius: 10px;
  background-color: var(--shiny-chat-user-message-bg);
  max-width: 100%;
}

shiny-user-message[content_type=text],
shiny-chat-message[content_type=text] {
  white-space: pre;
  overflow-x: auto;
}

shiny-chat-input {
  --_input-padding-top: 0;
  --_input-padding-bottom: var(--_chat-container-padding, 0.25rem);
  margin-top: calc(-1 * var(--_input-padding-top));
  position: sticky;
  bottom: calc(-1 * var(--_input-padding-bottom) + 4px);
  padding-block: var(--_input-padding-top) var(--_input-padding-bottom);
}
shiny-chat-input textarea {
  --bs-border-radius: 26px;
  resize: none;
  padding-right: 36px !important;
  max-height: 175px;
}
shiny-chat-input textarea::placeholder {
  color: var(--bs-gray-600, #707782) !important;
}
shiny-chat-input button {
  position: absolute;
  bottom: calc(6px + var(--_input-padding-bottom));
  right: 8px;
  background-color: transparent;
  color: var(--bs-primary, #007bc2);
  transition: color 0.25s ease-in-out;
  border: none;
  padding: 0;
  cursor: pointer;
  line-height: 16px;
  border-radius: 50%;
}
shiny-chat-input button:disabled {
  cursor: not-allowed;
  color: var(--bs-gray-500, #8d959e);
}

/*
  Disable the page-level pulse when the chat input is disabled
  (i.e., when a response is being generated and brought into the chat)
*/
.shiny-busy:has(shiny-chat-input[disabled])::after {
  display: none;
}